@media print {
  .app {
    width: 98%;
  }

  .buttons {
    display: none;
  }

  .flex-container > div {
    border: 1px solid gray;
  }

  thead {
    border-bottom: 1px solid black;
  }
}

* {
  font-family: 'Menlo', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

.app {
  text-align: center;
  width: 99%;
  margin: 5px 0 0 5px;
}

.content {
  width: fit-content;
  margin: 0 5px;
}

.button-div {
  display: inline-block;
}

.header>div {
  display: inline-block;
  margin-bottom: 20px;
}

table, thead {
  width: 98%;
  border: 1px solid black;
  font-size: 12px;
}

td:nth-child(1) {
  width: 60px;
  font-size: 9px;
}

input {
  width: 90%;
  text-align: center;
}

.small-input {
  /* margin-top: 5px; */
  margin: auto;
  width: 40%;
  padding: 5px 5px;
  display: block;
}

thead {
  background-color: aliceblue;
  font-weight: 600;
}

span {
  font-size: 11px;
  font-weight: 300;
  vertical-align: middle;
  text-align: left;
}

.flex-container {
  display: flex;
  flex-wrap: nowrap;
  font-size: 10pt;
}

.flex-container>div {
  background-color: #f1f1f1;
  padding: 5px 0 5px 10px;
  width: 170px;
  margin: 0 0 25px 25px;
  text-align: left;
  border: 1px solid gray;
  /* border: 1px solid #149BF3; */
}

.flex-container>div:nth-child(5) {
  padding: 5px 0 0 0;
  width: 100px;
  text-align: center;
  /* background-color: transparent; */
}

.flex-container label {
  color: gray;
}

.flex-container .buttons {
  text-align: center;
  background-color: white;
  border-style: none;
  width: fit-content;
}

button {
  align-items: center;
  appearance: none;
  background-color: #3EB2FD;
  background-image: linear-gradient(1deg, #4F58FD, #149BF3 99%);
  background-size: calc(100% + 20px) calc(100% + 20px);
  border-radius: 100px;
  border-width: 0;
  box-shadow: none;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  /* display: inline-flex; */
  /* font-family: CircularStd, sans-serif; */
  font-size: 1rem;
  /* height: auto; */
  /* justify-content: center; */
  line-height: 1.75;
  padding: 6px 15px;
  /* position: relative; */
  text-align: center;
  text-decoration: none;
  transition: background-color .2s, background-position .2s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
}

button:active,
button:focus {
  outline: none;
}

button:hover {
  background-position: -20px -20px;
}

button:focus:not(:active) {
  box-shadow: rgba(40, 170, 255, 0.25) 0 0 0 .125em;
}